import Vector from './utils/vector';

import Engine from './utils/engine';
// import Engine from './utils/engine3d';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

class Pointer {
  constructor({ x = 0, y = 0 }) {
    this.position = new Vector({ x, y });
    this.velocity = new Vector().randomAngle().setMag(1);

    this.rotateAmount = Math.PI * 0.01;
    this.currentRotate = 0;
  }

  _outOfBounds({ x, y }, width, height) {
    return x < 0 || y < 0 || x >= width || y >= height;
  }

  _wrapPosition(width, height) {
    let { x, y } = this.position;
    if (x < 0) {
      this.position.x = width;
    }

    if (y < 0) {
      this.position.y = height;
    }

    if (x > width) {
      this.position.x = 0;
    }

    if (y > height) {
      this.position.y = 0;
    }
  }

  update(frame, width, height) {
    if (frame % 30 == 0) {
      let rd = Math.random();
      if (rd > 0.6) {
        if (rd < 0.8) {
          this.currentRotate = this.rotateAmount
        } else {
          this.currentRotate = -this.rotateAmount;
        }
      } else if (rd > 0.4) {
        this.currentRotate = 0;
      }
    }
    this.velocity.rotate(this.currentRotate);

    this.position.add(this.velocity);

    this._wrapPosition(width, height);
  }

  render() {
    ctx.save();
    ctx.beginPath();
    ctx.arc(this.position.x, this.position.y, 3, 0, Math.PI * 2);
    ctx.fill();

    let x = radius => (Math.cos(this.velocity.getAngle()) * radius) + this.position.x;
    let y = radius => (Math.sin(this.velocity.getAngle()) * radius) + this.position.y;

    ctx.beginPath();
    ctx.strokeStyle = 'red';
    ctx.moveTo(x(1), y(1));
    ctx.lineTo(x(10), y(10));
    ctx.stroke();
    ctx.restore();
  }
}

class LinePoint {
  constructor({ x, y, angle }) {
    this.position = new Vector({ x, y });
    this.angle = new Vector().setAngle(angle);
  }

  update({ x, y }, velocity) {
    this.position.set(x, y);
    this.angle.setAngle(velocity.getAngle());
  }

  render() {
    ctx.save();
    ctx.beginPath();
    ctx.fillStyle = 'black';
    ctx.arc(this.position.x, this.position.y, 3, 0, Math.PI * 2);
    ctx.fill();
    ctx.restore();

    let x = radius => (Math.cos(this.angle.getAngle()) * radius) + this.position.x;
    let y = radius => (Math.sin(this.angle.getAngle()) * radius) + this.position.y;

    ctx.beginPath();
    ctx.strokeStyle = 'red';
    ctx.moveTo(x(1), y(1));
    ctx.lineTo(x(10), y(10));
    ctx.stroke();
    ctx.restore();
  }
}

let startingX = WIDTH / 2;
let startingY = HEIGHT / 2;

const dot = new Pointer({
  x: startingX,
  y: startingY
});

const line = new Array(1000).fill(0).map(() => new LinePoint({ x: startingX, y: startingY, angle: 0 }));

function cascadeLine() {
  for (let i = line.length - 1; i >= 0; i--) {
    if (i == 0) {
      line[i].update(dot.position, dot.velocity);
    } else {
      line[i].update(line[i - 1].position, line[i - 1].angle)
    }
  }
}

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  dot.update(frame, width, height);
  // dot.render();

  // if (!debug) {
    cascadeLine();
  // }

  for (let i = 0; i < line.length; i++) {
    if (i % 25 == 0) {
      line[i].render();
    }
  }
});

app.start();
